import React from "react";
import {Toast} from "react-bootstrap";

export default function ToastContainer(props) {

    return(
        <Toast style={{position: "fixed", top: "10px", right: "10px"}} onClose={() => {props.setToast(false)}} show={props.visible}>
            <Toast.Header>
                <strong className="me-auto">Message Sent!</strong>
                <small>just now</small>
            </Toast.Header>
        </Toast>
    )
}