import React from "react";
import { Container, Row, Col, Image } from "react-bootstrap";

export default function AboutText() {
    return (
        <Row>
            <Col xs={12} md={8}>
                <Container className="d-flex justify-content-center flex-column h-100">
                    <span>Hello! I am</span>
                    <h2>Shun Jie</h2>
                    <span style={{padding: "0px 5px"}}>and I am currently...</span>
                    <h2>Working hard to become a developer!</h2>
                    <small className="text-muted">scroll down for more</small>
                </Container>
            </Col>
            <Col md={4} className="d-none d-md-block">
                <Container className="d-flex justify-content-center align-items-center">
                    <Image src="/images/shun_jie_gray.jpeg" height="80%" width="80%" />
                </Container>
            </Col>
        </Row>
    )
}