import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin, faCodepen } from "@fortawesome/free-brands-svg-icons";
import { Container, Row, Col } from "react-bootstrap";

export default function Footer() {
    let today = new Date();
    let year = today.getFullYear();

    return(
        <Container className="mb-3 mt-3">
            <Row>
                <Col className="text-center">
                    <Container className="d-flex align-items-center justify-content-center">
                        <a href="https://github.com/Ruin9999" target="_blank" rel="noreferrer">
                            <FontAwesomeIcon icon={faGithub} size="lg" color="#6e5494"/>
                        </a>
                        &nbsp;
                        <a href="https://www.linkedin.com/in/shun-jie-yong-9009bb164/" target="_blank" rel="noreferrer">
                            <FontAwesomeIcon icon={faLinkedin} size="lg" color="#0E76A8"/>
                        </a>
                        &nbsp;
                        <a href="https://codepen.io/ruin9999" target="_blank" rel="noreferrer">
                            <FontAwesomeIcon icon={faCodepen} size="lg"/>
                        </a>
                    </Container>
                </Col>
            </Row>
            <Row className="mt-1">
                <Col className="text-center">
                    <small>¬© {year} YONG SHUN JIE ALL RIGHTS RESERVED</small>
                </Col>
            </Row>
        </Container>
    )
}