import React from "react";
import "../../css/Timeline.scss";

import {Container} from "react-bootstrap";

export default function Timeline() {
    return(
        <Container>
            <h2 className="mb-4">My History üìú</h2>
            <ul className="timeline">
                <li className="event" data-date={"2000 - 2011"}>
                    <p className="d-inline-block">Sniffing apple juice, Drinking air</p>
                    <ul>
                        <li><small>Magically learnt how to eat, sleep and walk.</small></li>
                    </ul>
                </li>
                <li className="event" data-date={"2007 - 2012"}>
                    <p className="d-inline-block">Attended Pei Hwa Presbyterian</p>
                    <a className="d-inline-block m-1" href="https://peihwapresbyterianpri.moe.edu.sg/" target="_blank" rel="noreferrer"><small>link</small></a>
                    <ul>
                        <li><small>Joined the Robotics club.</small></li>
                        <li><small>Learnt about visual scripting through the help of LEGO NXT.</small></li>
                    </ul>
                </li>
                <li className="event" data-date={"2013 - 2017"}>
                    <p className="d-inline-block">Went to Fajar Secondary</p>
                    <a className="d-inline-block m-1" href="https://fajarsec.moe.edu.sg/" target="_blank" rel="noreferrer"><small>link</small></a>
                    <ul>
                        <li><small>Joined the Scouts uniformed group (UG).</small></li>
                        <li><small>Was a member of the Student Council (SC).</small></li>
                        <li><small>Picked up leadership and planning skills, learning how to put myself in the shoes of others.</small></li>
                    </ul>
                </li>
                <li className="event" data-date={"2018 - 2020"}>
                    <p className="d-inline-block">Spead my wings at Singapore Polytechnic</p>
                    <a className="d-inline-block m-1" href="https://www.sp.edu.sg/engineering-cluster/eee/courses/full-time-diplomas/computer-engineering/overview" target="_blank" rel="noreferrer"><small>link</small></a>
                    <ul>
                        <li><small>Pursued a diploma in Computer Engineering.</small></li>
                        <li><small>Obtained a scholarship from Singtel.</small></li>
                        <li><small>Joined the Disc Jockey club (SPDJ).</small></li>
                        <li><small>Ventured further into Software Development where I gained a stronger grasp on AI Technologies, Data Structures and Algorithms and Web / Game Development.</small></li>
                    </ul>
                </li>
                <li className="event" data-date={`2020 - Present`}>
                    <p className="d-inline-block">Enlisted into the Singapore Army</p>
                    <a className="d-inline-block m-1" href="https://www.mindef.gov.sg/web/portal/army/our-forces/formations/formations-detail/transport/transport" target="_blank" rel="noreferrer"><small>link</small></a>
                    <ul>
                        <li><small>Joined the Transport Vocation and currently on course to learn how to drive.</small></li>
                    </ul>
                </li>
            </ul>

        </Container>
    )
}