import React, {useState} from "react";
import { Container, Row, Col, Form} from "react-bootstrap";
import emailjs from "emailjs-com";

export default function Contact(props) {

    const [message, setMessage] = useState("");
    const [name, setName] = useState("");

    function HandleChange(e) {
        switch(e.target.name) {
            case "name":
                setName(e.target.value);
                break;
            case "message":
                setMessage(e.target.value);
                break;
        }
    }

    function HandleClick(e) {
        e.preventDefault();
        window.open(`mailto:b14ktoss@gmail.com?subject=Let's talk!`);
    }

    function HandleSubmit(e) {
        e.preventDefault();

        emailjs.send("service_g8snmcq","template_cz805a6",{
            firstName: name,
            message: message
        }, "user_gBREhfn77piuhd9XrUZdH")
        .then(() => {
            props.setToast(true);
            console.log("Success!");
        }, err => console.log(err.text));

    }

    return (
        <Container className="w-100">
            <Row className="align-items-center">
                <Col md={6}>
                    <Container className="d-inline-flex justify-content-center">
                        <h2>Let's have a chat!</h2>
                        <h2 className="swivleIcon">üç∫</h2>
                    </Container>
                </Col>

                <Col md={6}>
                    <Form onSubmit={HandleSubmit}>
                        <Form.Label>Name <small className="required">*</small></Form.Label>
                        <Form.Control name="name" type="text" placeholder="Shun Jie" size="sm" onChange={HandleChange} value={name} required/>

                        <Form.Label className="mt-2">Message <small className="required">*</small></Form.Label>
                        <Form.Control name="message" as="textarea" style={{height: "100px"}} placeholder="..." size="sm" onChange={HandleChange} value={message} required/>

                        <Container className="mt-2 btn">
                            <Form.Control type="submit" value="Send ‚úâÔ∏è"/>
                        </Container>

                        <Container className="text-center">
                            <a href="#email" onClick={HandleClick}><small>or send me a personal email...</small></a>
                        </Container>
                    </Form>
                </Col>
            </Row>
        </Container>
    )
}