import React from "react";
import { Container, Row, Col, Image} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCog } from "@fortawesome/free-solid-svg-icons";


export default function Skills() {
    return(
        <Row>
            <Container className="text-center mb-4">
                <h2>Skillset <h2 className="d-inline-block rotateIcon"><FontAwesomeIcon icon={faCog} size="lg"/></h2></h2>
            </Container>
            <Container className="text-center d-flex justify-content-center align-items-center">
                <Row>
                    <Col className="mt-3" sm={12} md={6}>
                        <h5>Languages</h5>
                        <Container className="customCard text-center d-flex align-items-center justify-content-center p-3" style={{height: "85%"}}>
                            <Row>
                                <Col className="m-1">
                                    <a href="https://developer.mozilla.org/en-US/docs/Web/JavaScript" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/javascript.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://docs.microsoft.com/en-us/dotnet/csharp/" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/csharp.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://www.php.net/" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/php.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                            </Row>
                        </Container>
                    </Col>
                    <Col className="mt-3" sm={12} md={6}>
                        <h5>Technologies</h5>
                        <Container className="customCard text-center d-flex align-items-center justify-content-center p-3" style={{height: "85%"}}>
                            <Row>
                                <Col className="m-1">
                                    <a href="https://code.visualstudio.com/" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/visualcode.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://blender.org" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/blender.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://unity.com" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/unity.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://image-line.com" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/fl.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://nodejs.org" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/node.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://reactjs.org" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/react.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                                <Col className="m-1">
                                    <a href="https://threejs.org" target="_blank" rel="noreferrer">
                                        <Image className="hoverIcon" src="/icons/three.png" height="50px" width="50px" />
                                    </a>
                                </Col>
                            </Row>
                        </Container>
                    </Col>
                    <Container className="text-center mt-2">
                        <p>With many more to come!</p>
                    </Container>
                </Row>
            </Container>
        </Row>
    )
}