import React, {useState, useEffect} from 'react';
import './css/App.scss'

import Aos from "aos";
import "aos/dist/aos.css";

import {Container} from "react-bootstrap";
import {BrowserRouter as Router, Routes, Route} from "react-router-dom";

import CustomNav from "./components/Navbar";
import CustomFooter from "./components/Footer";
import ToastContainer from "./components/ToastContainer";
import Main from "./pages/Main";
import About from "./pages/About";
import Works from "./pages/Works";
import Contact from "./pages/Contact";

function App() {

  const [repoData, setRepoData] = useState([]);
  const [visibleToast, setVisibleToast] = useState(false);

  useEffect(() => {
    fetch("https://api.github.com/users/ruin9999/repos")
    .then(res => {
      if(res.status === 200) {
        return res.json();
      }
    })
    .then(data => setRepoData(data))
    .catch(err => console.log(err))
  }
  , [])

  useEffect(() => Aos.init({ duration: 500 }));

  return (
    <Router>
      <Container style={{minHeight: "100vh"}}>
        <CustomNav/>
        <ToastContainer visible={visibleToast} setToast={setVisibleToast}/>
        <Routes>
          <Route path="/" element={<Main setToast={setVisibleToast}/>} />
          <Route path="/about" element={<About />} />
          <Route path="/works" element={<Works data={repoData} />} />
          <Route path="/contact" element={<Contact setToast={setVisibleToast}/>} />
        </Routes>
        <CustomFooter />
      </Container>
    </Router>
  );
}

export default App;
