import React from "react";
import { Container } from "react-bootstrap";

import MainAbout from "../components/Main/About"; 
import MainContact from "../components/Main/Contact";

export default function MainPage(props) {
    return(
        <Container>
            <Container style={{minHeight: "90vh"}} className="d-flex justify-content-start align-items-center" data-aos="fade-up">
                <MainAbout />
            </Container>
            <Container style={{minHeight: "90vh"}} className="d-flex align-items-center" data-aos="fade-up">
                <MainContact setToast={props.setToast}/>
            </Container>
        </Container>
    )
}