import React from "react";
import { Container, Row, Col } from "react-bootstrap";

import RepoCard from "../components/RepoCard";

interface IWorks {
    data : any
}

export default function Works(props: IWorks) {

    const RepoList = props.data.map(repo => {
        return <Col style={{marginTop: "10px"}}>
            <RepoCard title={repo.name} des={repo.description} htmlUrl={repo.htmlUrl} imgUrl={repo.imgUrl} lang={repo.language}/>
        </Col>
    })

    return(
        <Container style={{minHeight : "85vh"}}>
            <Container data-aos="fade-up" className="mt-1">
                <h2 style={{borderBottom: "1px solid black", padding: "5px"}}>Projects</h2>
                <Row>
                    {RepoList}
                </Row>
            </Container>
        </Container>
    )
}