import React, {useState} from "react";
import {Container, Form, Row, Col} from "react-bootstrap";
import emailjs from "emailjs-com";

export default function ContactForm(props) {

    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [email, setEmail] = useState("");
    const [contact, setContact] = useState("");
    const [organization, setOrganization] = useState("");
    const [message, setMessage] = useState("");

    function HandleChange(e) {
        switch(e.target.name) {
            case "firstName":
                setFirstName(e.target.value);
                break;
            case "lastName":
                setLastName(e.target.value);
                break;
            case "email":
                setEmail(e.target.value);
                break;
            case "contact":
                setContact(e.target.value);
                break;
            case "org":
                setOrganization(e.target.value);
                break;
            case "message":
                setMessage(e.target.value);
                break;
        }
        console.log(e.target.name, e.target.value);
    }

    function HandleSubmit(e) {
        e.preventDefault();

        emailjs.send("service_g8snmcq","template_cz805a6",{
            firstName: firstName,
            lastName: lastName,
            email: email,
            contact: contact,
            org: organization,
            message: message,
        }, "user_gBREhfn77piuhd9XrUZdH")
        .then(() => {
            props.setToast(true);
            console.log("Success!");
        }, err => console.log(err.text));
    }

    return(
       <Form onSubmit={HandleSubmit}>
           <Form.Group className="mt-1">
            <Row>
                <Col sm={6}>
                    <Form.Label>First Name <small className="required">*</small></Form.Label>
                    <Form.Control name="firstName" value={firstName} type="text" placeholder="Shun Jie" size="sm" onChange={HandleChange} required/>
                </Col>
                <Col sm={6}>
                    <Form.Label>Last Name</Form.Label>
                    <Form.Control name="lastName" value={lastName} type="text" placeholder="Yong" size="sm" onChange={HandleChange}/>
                </Col>
            </Row>
            </Form.Group>
            <Form.Group className="mt-1">
                <Row>
                    <Col sm={6}>
                        <Form.Label>Email</Form.Label>
                        <Form.Control name="email" value={email} placeholder="Your Email" size="sm" onChange={HandleChange}/>
                    </Col>
                    <Col sm={6}>
                        <Form.Label>Contact no.</Form.Label>
                        <Form.Control name="contact" value={contact} placeholder="+65" size="sm" onChange={HandleChange}/>
                    </Col>
                </Row>
            </Form.Group>
            <Form.Group className="mt-1">
                <Form.Label>Organization</Form.Label>
                <Form.Control name="org" value={organization} size="sm" onChange={HandleChange} />
            </Form.Group>
            <Form.Group className="mt-1">
                <Form.Label>Your Message <small className="required">*</small></Form.Label>
                <Form.Control name="message" value={message} as="textarea" style={{height: "100px"}} placeholder="..." size="sm" onChange={HandleChange}  required/>
                <Form.Text>I will not share any of this information with anyone.</Form.Text>
            </Form.Group>
            <Container className="mt-2 btn">
                <Form.Control type="submit" value="Reach out üó£"/>
            </Container>
       </Form>
    )
}