import React from "react";
import { Card, Image } from "react-bootstrap";

interface RepoCardType {
    title: string,
    des: string,
    htmlUrl: string,
    lang: string,
    imgUrl? : string
}

export default function RepoCard(props : RepoCardType){
    let langImg;

    switch(props.lang) {
        case "C++":
            langImg = "/icons/cplusplus.png";
            break;
        case "JavaScript":
            langImg = "/icons/javascript.png";
            break;
        case "Java":
            langImg = "/icons/java.png";
            break;
        case "HTML":
            langImg = "icons/html.png";
            break;
        case "PHP":
            langImg = "icons/php.png";
            break;
        default:
            langImg = null;
    }

    return(
        <Card>
            {props.imgUrl ? <Card.Img src={props.imgUrl} /> : null}
            <Card.Body>
                <Card.Title>
                    {props.title}
                    {" "}
                    <Image style={{maxHeight: "1em"}}src={langImg} fluid />
                </Card.Title>
                <Card.Text>
                    {props.des}
                </Card.Text>
                <a href={props.htmlUrl} target="_blank" rel="noreferrer"><small>Learn more</small></a>
            </Card.Body>
        </Card>
    )
}