import React from "react";
import { Container } from "react-bootstrap";

import AboutText from "../components/About/AboutText";
import Timeline from "../components/About/Timeline";
import Skills from "../components/About/Skills";

export default function About() {

    return (
        <Container style={{minHeight: "85vh"}} className="d-flex align-items-center">
            <Container className="w-100">
                <Container style={{minHeight: "90vh"}} className="d-flex align-items-center justify-content-center">
                    <AboutText />
                </Container>
                
                <Container style={{minHeight: "90vh"}} className="d-flex align-items-center justify-content-center" data-aos="fade-up">
                    <Skills />
                </Container>

                <Container style={{minHeight: "90vh"}} className="d-flex align-items-center justify-content-center text-center" data-aos="fade-up">
                    <Timeline />
                </Container>
            </Container>
        </Container>
    )
}